body {
  margin: 0;
  padding: 0;
  background: var(--primary-main) ;
  color: var(--white);
  font-family: "Montserrat", sans-serif;
  overflow: hidden;
  width: 100vw;
}

#root {
  color: var(--white);
  margin: 0;
  position: relative;
  min-height: 100vh;
  width: 100vw;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-height: 100%;
  max-width: 100%;
}

:root {
  --default-background-color: #ffffff;

  --gray-light: #A5A5A5;
  --gray: #6c6c79;
  --black: #34343d;
  --white: #ffffff;

  --primary-main: #001724;
  --primary-light: #293c4b;
  --primary-dark: #01080E;

  --secondary-main: #68D6F1;
  --secondary-light: #a5e8f7;
  --secondary-dark: #006c85;

  --accent-blue: #68D6F1;
  --accent-yellow: #F1D568;
  --accent-green: #91F168;
  --accent-red: #f26868;
}

.primaryTitle_main {
  color: var(--primary-main);
  font-size: 41px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.primaryTitle_SM {
  color: var(--secondary-main);
  font-size: 41px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.primaryTitle_White {
  color: var(--white);
  font-size: 41px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.primaryTitle_Black {
  color: var(--black);
  font-size: 41px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.primaryTitle {
  font-size: 41px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.secondaryTitle {
  font-size: 40px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.primarySubtitle {
  font-size: 25px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.primarySubtitlePM {
  font-size: 25px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  color: var(--primary-main);
}
.primarySubtitleW {
  font-size: 25px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  color: var(--white);
}

.secondarySubtitle {
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.secondarySubtitleW {
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  color: var(--white);
}

.secondarySubtitlePM {
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  color: var(--primary-main);
}

.secondarySubtitleSM {
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  color: var(--secondary-main);
}

.mediumText {
  font-size: 17px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.mediumTextSM {
  font-size: 17px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  color: var(--secondary-main);
}

.generalText {
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}